import React from "react"
import { Link } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"
import NavigationBar from "../components/navigationBar"
import { Splide, SplideSlide } from "@splidejs/react-splide"
import "@splidejs/splide/dist/css/themes/splide-default.min.css"
import GenericTile from "../components/genericTile"
import Typewriter from "typewriter-effect"

const ContactPage = () => (
  <Layout>
    <SEO title="Contact Me" />
    <NavigationBar active="contact" color="#aa4a12" />
    <div className="description">
      <Typewriter
        onInit={typewriter => {
          typewriter
            .changeDeleteSpeed(2)
            .changeDelay(30)
            .pauseFor(300)
            .typeString(
              "Feel free to reach out to me by any of the methods below."
            )
            .start()
        }}
      />
    </div>
    <Splide>
      <SplideSlide>
        <div>
          <GenericTile name="Email" description="canamsenquiries@gmail.com" />
        </div>
      </SplideSlide>
      <SplideSlide>
        <div>
          <GenericTile
            name="Socials"
            description="LinkedIn - Chiamaka Anamekwe"
          />
        </div>
      </SplideSlide>
    </Splide>
  </Layout>
)

export default ContactPage
